@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Lora:400,400i,700,700i|Montserrat:400,400i,700,700i|Open+Sans:400,400i,700,700i|Roboto:400,400i,700,700i");
.cert-font__roboto {
  font-family: 'Roboto', sans-serif; }

.cert-font__opensans {
  font-family: 'Open Sans', sans-serif; }

.cert-font__montserrat {
  font-family: 'Montserrat', sans-serif; }

.cert-font__lora {
  font-family: 'Lora', serif; }

.cert-wokrspace {
  overflow: auto; }
  .cert-wokrspace__controls {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 20px 0; }
  .cert-wokrspace__control {
    display: flex;
    align-items: center;
    margin-bottom: 10px; }
    .cert-wokrspace__control h5 {
      font-size: 17px;
      margin: 0 10px 0 0; }
    .cert-wokrspace__control span {
      margin-right: 7px; }
    .cert-wokrspace__control .btn {
      margin-right: 10px; }
  .cert-wokrspace__content {
    height: 793.7px;
    width: 1122.5px;
    background-color: #b6e6e4;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: relative; }
    .cert-wokrspace__content .cert-name,
    .cert-wokrspace__content .cert-number,
    .cert-wokrspace__content .cert-course,
    .cert-wokrspace__content .cert-date,
    .cert-wokrspace__content .cert-duration {
      position: absolute;
      color: #000;
      background: transparent;
      cursor: move;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none; }
      .cert-wokrspace__content .cert-name.cert-bordered,
      .cert-wokrspace__content .cert-number.cert-bordered,
      .cert-wokrspace__content .cert-course.cert-bordered,
      .cert-wokrspace__content .cert-date.cert-bordered,
      .cert-wokrspace__content .cert-duration.cert-bordered {
        outline: 1px dashed #777; }

.cert-controls__header {
  font-size: 1.25rem;
  padding: 20px 0; }

.cert-controls .form-group {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px dashed #eee; }
  .cert-controls .form-group label {
    margin: 0 1rem 0 0; }
  .cert-controls .form-group input {
    width: 100px; }
    .cert-controls .form-group input[type="checkbox"] {
      width: auto; }
  .cert-controls .form-group select {
    width: 200px; }

.cert-controls .custom-file-control::before {
  content: "Выбрать" !important; }

.cert-controls .custom-file-control::after {
  content: "Выбрать файл..." !important; }

.cert-controls input[type="color"] {
  -webkit-appearance: none;
  height: 50px; }
